@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600;700&display=swap);
:root {
  /* Screen Sizes */
  --mobile-width: 375px;
  --mobile-content-width: 375px;
  --desktop-width: 1366px;
  --desktop-content-width: 700px;
  /* Colours */
  --light-color: white;
  --dark-color: hsl(229, 25%, 31%);
  --score-color: hsl(229, 64%, 46%);
  --header-outline-color: hsl(217, 16%, 45%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  background-image: radial-gradient(
    circle at top,
    hsl(214, 47%, 23%),
    hsl(237, 49%, 15%)
  );
  /* background-size: 100%; */
  background-repeat: no-repeat;

  font-family: 'Barlow Semi Condensed', sans-serif;

  min-width: 375px;

  min-width: var(--mobile-width);
  color: white;
  color: var(--light-color);
}

body {
  max-width: 375px;
  max-width: var(--mobile-content-width);
  height: 100vh;
  margin: auto;
}

#root {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: center;

  width: 100%;
  height: 100%;
}

.gamePanel {
  position: relative;
  width: 375px;
  width: var(--mobile-content-width);
  margin: auto;
  flex-grow: 1;
}

footer {
  display: flex;
  justify-content: center;
  width: 375px;
  width: var(--mobile-content-width);
  padding: 30px;
}

 /*
  * Desktop styles
  */
@media only screen and (min-width: 1366px) {
  body {
    width: 1366px;
    width: var(--desktop-width);
  }
  footer {
    justify-content: flex-end;
    width: 1366px;
    width: var(--desktop-width);
  }
}


header {
  display: flex;
  position: relative;
  border: 3px solid var(--header-outline-color);
  border-radius: 10px;

  margin: 30px;
  padding: 17px;
  width: calc(var(--mobile-content-width) - 60px);

  font-weight: 700;
  justify-content: space-between;
  align-items: center;
}

.title-img {
  height: 48px;
}

/*
 * Desktop styles
 */
@media only screen and (min-width: 1366px) {
  header {
    margin: 45px;
    padding: 22px;
    width: calc(var(--desktop-content-width) - 90px);
  }
  .title-img {
    height: 95px;
  }
}

.score-panel {
  display: inline-block;
  background-color: var(--light-color);
  color: var(--score-color);
  border-radius: 5px;

  padding: 10px;
  width: 80px;
  height: 72px;

  text-transform: uppercase;
}
.score-panel p {
  text-align: center;
  margin: 0;
}
.score-title {
  font-size: 10px;
  letter-spacing: 1.5px;
}
.score-display {
  color: var(--dark-color);
  line-height: 40px;
  font-weight: 700;
  font-size: 40px;
}

/*
 * Desktop styles
 */
@media only screen and (min-width: 1366px) {
  .score-panel {
    border-radius: 10px;
    padding: 15px;
    width: 150px;
    height: 115px;
  }
  .score-title {
    font-size: 18px;
  }
  .score-display {
    font-size: 64px;
    line-height: 60px;
  }
}

/* Sizes for chooser control in the basic game */
.chooser.basic {
  --diameter: 350px;
  --item-size: 130px;
  --border-base: 15px;
  margin-top: calc(-1 * (var(--item-size) /4))
}
/* Sizes for chooser control in the spock game */
.chooser.spock {
  --diameter: 350px;
  --item-size: 100px;
  --border-base: 10px;
}

.chooser {
  --offset: calc((var(--diameter) - var(--item-size)) / 2);
}

.display {
  --item-size: 130px;
  --border-base: 15px;
}

.badge {
  --icon-size: var(--item-size);
  --border-width: var(--border-base);
  --image-size: calc(var(--icon-size) / 2.5);
  --anim-time: 0.25s;

  position: relative;
  width: calc(var(--icon-size) - (var(--border-width) * 2));
  height: calc(var(--icon-size) - (var(--border-width) * 2));
  margin: var(--border-width);
  border-radius: 50%;
  border: none;
  color: transparent;
  background: white;
  box-shadow: 0 5px #00000025 inset;

  transition: var(--anim-time);
}

button.badge:hover {
  --icon-size: calc(var(--item-size) * 1.15);
  --border-width: calc(var(--border-base) * 1.15);
}

.badge::before {
  content: '';

  position: absolute;
  z-index: -1;

  top: calc(-1 * var(--border-width));
  left: calc(-1 * var(--border-width));
  width: var(--icon-size);
  height: var(--icon-size);
  border-radius: inherit;

  background: linear-gradient(0deg,
    var(--start-color) 0%,
    var(--end-color) 100%);
  box-shadow: 0 5px var(--shadow-color);

  transition: var(--anim-time);
}

.badge.empty {
  background-color: rgba(0,0,0,0.25);
  --shadow-color: none;
  --start-color: none;
  --end-color: none;
}
.badge.rock {
  background: white url(/rock-paper-scissors/static/media/icon-rock.476e90a9.svg) center/var(--image-size) no-repeat;
  --shadow-color: hsl(349, 70%, 36%);
  --start-color: hsl(349, 71%, 52%);
  --end-color: hsl(349, 70%, 56%);
}
.badge.paper {
  background: white url(/rock-paper-scissors/static/media/icon-paper.8b57a6b1.svg) center/var(--image-size) no-repeat;
  --shadow-color: hsl(230, 89%, 46%);
  --start-color: hsl(230, 89%, 62%);
  --end-color: hsl(230, 89%, 65%);
}
.badge.scissors {
  background: white url(/rock-paper-scissors/static/media/icon-scissors.3b1a5d7e.svg) center/var(--image-size) no-repeat;
  --shadow-color: hsl(40, 84%, 36%);
  --start-color: hsl(39, 89%, 49%);
  --end-color: hsl(40, 84%, 53%);
}
.badge.lizard {
  background: white url(/rock-paper-scissors/static/media/icon-lizard.0f83914c.svg) center/var(--image-size) no-repeat;
  --shadow-color: hsl(261, 72%, 46%);
  --start-color: hsl(261, 73%, 60%);
  --end-color: hsl(261, 72%, 63%);
}
.badge.spock {
  background: white url(/rock-paper-scissors/static/media/icon-spock.f4d7eee8.svg) center/var(--image-size) no-repeat;
  --shadow-color: hsl(189, 59%, 36%);
  --start-color: hsl(189, 59%, 53%);
  --end-color: hsl(189, 58%, 57%);
}

/*
 * Desktop Styles
 */
@media only screen and (min-width: 1366px) {
  .chooser.basic {
    --diameter: 500px;
    --item-size: 200px;
    --border-base: 22px;
  }
  .chooser.spock {
    --diameter: 500px;
    --item-size: 150px;
    --border-base: 16px;
  }
}

/*
 * Note: Requires the following variables to be set,
 *        preferably on the .chooser parent:
 *
 *  --diameter: Diameter of chooser control
 *  --item-size: Diameter of choice items
 *  --offset: The distance from the chooser center that items should appear
 *
 * List requires:
 *  --n: The total number of items
 *
 * Items require:
 *  --i: The item's list position (1-n)
 */

.chooser {
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
}
.chooser > .circle {
  position: absolute;
  left: calc(var(--item-size) / 2);
  top: calc(var(--item-size) / 2);
  width: calc(var(--offset) * 2);
  height: calc(var(--offset) * 2);

  border-radius: 50%;
  border: 15px solid #00000025;
}

.chooser ul {
  position: relative;
  list-style: none;
  margin: 0;
  padding: 0;

  width: var(--diameter);
  height: var(--diameter);
}

.chooser li {
  position: absolute;
  top: 50%;
  left: 50%;

  /*
    --s = chooser start rotation
    --a = item angle
  */
  --s: calc(0.75 * (1 / var(--n)));
  --a: calc((var(--s) * 1turn) + var(--i) * (1turn / var(--n)));

  -webkit-transform:
    translate(-50%, -50%)         /* Move item to center */
    rotate(var(--a))              /* Rotate to align with required position */
    translate(var(--offset))      /* Move to circle boundary */
    rotate(calc(-1 * var(--a)));

          transform:
    translate(-50%, -50%)         /* Move item to center */
    rotate(var(--a))              /* Rotate to align with required position */
    translate(var(--offset))      /* Move to circle boundary */
    rotate(calc(-1 * var(--a)));  /* Realign rotation with page */
}

.display {
  display: flex;
  flex-flow: row wrap;
  height: 100%;
  justify-content: space-around;
}
.player {
  display: flex;
  flex-flow: column;
  width: 50%;
  padding: var(--border-basis);
  margin-bottom: 30px;
  align-items: center;
}
.player.one {
  order: 1;
}
.player.two {
  order: 2;
}

.player > .name {
  margin-top: 20px;
  text-transform: uppercase;
}

.winner-box {
  display: flex;
  flex-flow: column;
  align-items: center;

  width: 100%;

  order: 3;
}

.winner-box > h2 {
  text-transform: uppercase;
}

.winner-box > button {
  text-transform: uppercase;
  padding: 10px;

  border: none;
  border-radius: 10px;
}

.settings-icon {
  background: none;
  border: none;
}

.settings-icon:hover {
  -webkit-filter: drop-shadow(0 0 5px white);
          filter: drop-shadow(0 0 5px white);
}

.settings-icon img {
  width: 48px;
  height: 48px;
}

.settings-dialog {
  position: absolute;
  top: calc(100% + 3px);

  overflow: hidden;

  left: 5%;
  height: 0;
  width: 90%;
  color: black;
  z-index: 99;

  background-color: white;
  border-radius: 0 0 20px 20px;

  box-shadow: 0 5px 5px #00000060, inset 0 20px 20px -20px rgba(0,0,0,0.8);

  transition: height 1s;
}

.settings-dialog.show {
  height: 200px;
}

.settings-wrapper {
  padding: 10px;
}

.settings-dialog h2 {
  color: var(--dark-color);
  text-transform: uppercase;
  text-align: center;
}

.modal-wrapper {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  background-color: rgba(0,0,0,0.5);

  z-index: 999;
}

.modal-dialog {
  position: relative;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  padding: 25px;

  background-color: white;
}

.modal-close {
  border: none;
  background: transparent;
}

/*
 * Desktop styles
 */
@media only screen and (min-width: 1366px) {
  .modal-dialog {
    position: relative;
    top: 50%;
    left: 50%;
    width: 50%;
    height: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

    border-radius: 10px;
    background-color: white;
  }
}

.modal-trigger {
  text-transform: uppercase;

  width: 130px;
  height: 42px;

  color: white;
  background: transparent;
  border: 2px solid white;
  border-radius: 10px;
}

.modal-trigger:hover {
  -webkit-filter: drop-shadow(0 0 5px white);
          filter: drop-shadow(0 0 5px white);
}

.modal-dialog {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  background-color: white;
}

.rules-title {
  order: 1;
  color: var(--dark-color);
  text-transform: uppercase;
  text-align: center;
}

.rules-image {
  order: 2;
  display: block;
  object-fit: contain;
  margin: 20px 0;
}

.rules-close {
  order: 3;
}

@media only screen and (min-width: 1366px) {
  .modal-dialog {
    position: relative;
    flex-flow: row wrap;

    width: 400px;
    height: auto;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  .rules-image {
    order: 3;
    margin: 20px auto;
  }
  .rules-close {
    order: 2;
  }
}

